.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px 0;
}

.infected {
  border-bottom: 20px solid rgba(0, 0, 255, 0.747);
  background-image: linear-gradient(rgb(95, 111, 255),rgb(17, 25, 80));
  opacity: 0.9;
}

.recovered {
  border-bottom: 20px solid rgba(0, 255, 0, 0.692);
  background-image: linear-gradient(rgb(121, 253, 172),rgb(36, 73, 50));
  opacity: 0.7;
}

.deaths {
  border-bottom: 20px solid rgba(255, 0, 0, 0.767);
  background-image: linear-gradient(rgb(255, 152, 152),rgb(73, 43, 43));
  opacity: 0.7;
}
.infected:hover
{
  background-image: linear-gradient(rgb(119, 133, 255),rgb(108, 116, 196));
  opacity: 0.9;
}
.recovered:hover
{
  background-image: linear-gradient(rgb(121, 253, 172),rgb(70, 158, 104));
  opacity: 0.9;
}
.deaths:hover
{
  background-image: linear-gradient(rgb(255, 152, 152),rgb(161, 48, 48));
  opacity: 0.9;
}