body {
  background-image: url(./images/world.jpg);
  background-size:cover;
  opacity: none;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.img {
  border-radius: 12px;
  background-image: linear-gradient(rgba(255, 255, 255, 0.452),rgba(255, 51, 51, 0.466));
  width: 370px;
  margin-top: 05px;
  opacity: 0.8;
}

@media (max-width: 770px) {
  .container {
    margin: 0 10%;
  }

  .img {
    width: 100%;
  }
}

footer {
  color: rgb(182, 182, 182);
  padding: 2px 10px;
  border-radius: 5px;
  background: linear-gradient(to right, rgb(2, 7, 39), rgb(2, 7, 80));
  font-family: Poppins,Verdana, Arial, sans-serif;
  font-size: small;
  margin-top: 50px;
  opacity: 0.5;
}
