.container {
  background-image: linear-gradient(rgb(175, 175, 175),rgb(197, 197, 197));
  border-radius: 3px;
  display: flex;
  justify-content: center;
  width: 80%;
}
.container:hover
{
  background-image: linear-gradient(rgb(255, 255, 255),rgb(192, 192, 192));
}

@media (max-width: 770px) {
  .container {
    width: 100%;
  }
}
